
<div class="prime-section">
  <empty-section
    :variation="'prime'"
    :titleSlot="true"
    :subtitle="subtitle"
    :hasLink="true"
  >
    <div slot="title">
      <Badge :align-left="true" :variant="'prime-alt'"></Badge>Feature
    </div>
    <div slot="link">
      <Button
        @click="$emit('click')"
        :tag="'a'"
        :variation="'link'"
        :href="href"
        :title="'Try Prime Now'"
      ></Button>
    </div>
  </empty-section>
</div>
