
<div class="s-call-to-action s-call-to-action--nav" :style="callToActionBg">
  <div class="s-title">
    <slot name="title"></slot>
  </div>
  <div>{{ description }}</div>
  <div class="s-call-to-action__extras">
    <slot name="extras"></slot>
  </div>
  <Button
    :variation="buttonVariation"
    :size="'small'"
    :title="buttonTitle"
    :href="buttonHref"
    :to="buttonTo"
    :tag="buttonTag"
    @click="$emit('click')"
  ></Button>
</div>
